

function InformacijeIsporuka() {

window.scrollTo({
    top: 100,
    left: 100,
    behavior: 'smooth'
    });
  return (
    <div>
      <h3>Isporuka</h3>
        <div>
            Minimalni iznos porudbine ne moe da bude manji od 1000rsd. Porudbine koje nemaju naveden minimum nee se isporuivati. 
            Trokovi isporuke na celoj teritoriji Srbije posebno se zaraunavaju i plaaju, u skladu sa cenovnikom Post Express slube i iznose od 450,00 dinara, pa navie,
            zavisno od teine i vrednosti poiljke. Maksimalna teina paketa za slanje PostExpress slubom je 20 kg, u kutijama ije dimenzije ne prelaze 60x50x50cm.
            U suprotnom se poiljka alje kao CC paket, koja ima malo dui rok isporuke. Ukoliko ima vie paketa za svaki se plaaju trokovi isporuke.
            Prilikom prijema plaa se vrednost robe + trokovi isporuke. Kako bi poiljka bila sigurno i brzo dostavljena do kupca, potrebno je da kupac navede tane podatke 
            prilikom formiranja porudbine. Vreme isporuke je od 2-5 radnih dana. 
        </div>
    </div>
  );
}

export default InformacijeIsporuka;
